












































































/*登录框*/
.login_box[data-v-c6bcac80] {
  z-index: 99;
  position: absolute;
  width: 380px;
  height: 540px;
  top: 50%;
  left: 50%;
  margin-left: -190px;
  margin-top: -270px;
  border-radius: 6px;
  background-color: #fff;
  box-shadow: 0 2px 10px #999;
}
.login_close[data-v-c6bcac80] {
  position: absolute;
  top: 0;
  right: 0;
  width: 64px;
  height: 64px;
  background: url(../../assets/img/qrcode.png) no-repeat right top;
  background-size: 100% 100%;
  border-top-right-radius: 5px;
  cursor: pointer;
  z-index: 99;
}
/*登录*/
.login_panel[data-v-c6bcac80] {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 270px;
  height: 540px;
  padding: 0 55px;
  transform: translate(-50%, -50%);
  /* background: #fff; */
  border-radius: 6px;
  overflow: hidden;
}
.login_panel .login_title[data-v-c6bcac80] {
  text-align: center;
}
.login_panel .login_title img[data-v-c6bcac80] {
  margin-top: 60px;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  padding: 10px;
  border: 3px solid #d7e8fc;
}
.login_panel .login_title p[data-v-c6bcac80] {
  margin-top: 15px;
  color: #999999;
  font-size: 15px;
}
.login_panel label[data-v-c6bcac80] {
  display: block;
  font-size: 12px;
  line-height: 18px;
  color: #a9a8a5;
  margin-top: 10px;
}
.login_panel input[data-v-c6bcac80] {
  display: inline;
  height: 42px;
  padding: 0 5%;
  line-height: 42px;
  font-size: 14px;
  color: #333333;
  border-radius: 4px;
  outline: 0;
  border: 0;
  width: 90%;
  background: #d7e8fc;
}
/* 按钮 */
.login_panel .bt[data-v-c6bcac80] {
  margin-top: 35px;
  width: 100%;
  color: #ffffff;
  background: #379df6;
  cursor: pointer;
}
.login_panel .bt[data-v-c6bcac80]:hover {
  background-color: #2f86f6;
}



